<template>
  <div class="mainBox fx-ver" :style="{'transform':tf }" v-show="ChatRoom.min">
    <div id="header" class="header fx-ac fx-pj">
      <div class="fx-ac">
        <button @click="connect">连接</button>
        <button @click="exit">断开</button>
        <div :class="self.status === 'onLine'?'onLine':'offline'"></div>
        <span class="name"
              @click="openDialog('nickname')">{{ nickNameMap[self.clientName] || self.clientName }}</span>
      </div>
      <div class="fx-ac">
        <span class="fa fa-minus-square fz18 middleButton" @click="minimize"></span>
        <span class="fa fa-square fz18 middleButton"></span>
        <span class="fa fa-times fz18 middleButton" @click="closeApp"></span>
      </div>
    </div>
    <div class="flex main">
      <div class="left">
        <div class="fx-ver memberList">
          <div :class="`member fx-ac fx-pj ${self.curTalk === 'all' ? 'check':''}`" @click="groupChat">
          <span class="fx-ac">
          <HeadImg :item="groupImg"></HeadImg>
          <span class=" fz18">大型多人激情聊天现场</span>
          </span>
            <span class="unread"
                  v-if="unreadMsgCountMap['all']">{{
                unreadMsgCountMap['all'] > 0 ? unreadMsgCountMap['all'] : ''
              }}</span>
          </div>
          <div :class="`member fx-ac fx-pj ${self.curTalk === item ? 'check':''}`" v-for="(item,idx) in memberList.list"
               :key="idx" @click="privateChat(item)">
          <span class="fx-ac">
          <HeadImg :item="headImgMap[item]"></HeadImg>
          <span>{{ nickNameMap[item] || item }}</span>
          </span>
            <span class="unread"
                  v-if="unreadMsgCountMap[item]">{{ unreadMsgCountMap[item] > 0 ? unreadMsgCountMap[item] : '' }}</span>
          </div>
        </div>
      </div>
      <div class="right fx-ver">
        <div class="chatName">
          <div class="fx-ac" v-if="self.curTalk === 'all'">
            <HeadImg :item="groupImg" @click.native="openDialog('groupImg')"></HeadImg>
            <span class=" fz22">大型多人激情聊天现场</span>
          </div>
          <div class="fx-ac" v-if="self.curTalk !== 'all'">
            <HeadImg :item="headImgMap[self.curTalk]"></HeadImg>
            <span class="fz22">{{ nickNameMap[self.curTalk] || self.curTalk }}</span>
          </div>
        </div>
        <div class="top" id="talkBox">
          <div class="oneLine" v-for="(item,idx) in messageList" :key="idx">
            <Message :item=item></Message>
          </div>
        </div>
        <div class="middle fx-ac ">
          <el-popover
              placement="top-start"
              width="300"
              trigger="hover">
            <div class="fx-bm emojiBox">
              <span class="emoji fz18" v-for="(item,idx) in emojiList" :key="idx" @click="text+=item">{{ item }}</span>
            </div>
            <span slot="reference" class="middleButton">😃</span>
          </el-popover>
          <span v-if="self.curTalk !== 'all'" class="fa fa-file-image-o fz22 middleButton"
                @click="openDialog('binaryImgSend')" title="图片"></span>
          <span v-if="self.curTalk !== 'all'" class="fa fa-file-text fz22 middleButton"
                @click="openDialog('binaryFile')" title="文件"></span>
        </div>
        <div class="down">
        <textarea v-model="text" class="textarea fz18" @keyup.enter="sendMsg">
        </textarea>
        </div>
      </div>
    </div>
    <el-dialog
        title="提示"
        :visible.sync="showBox"
        width="30%"
        :modal="false">
      <label v-if="type==='nickname'">
        <span>输入昵称：</span>
        <input type="text" v-model="msg" maxlength="20" @keyup.enter="setNickname" class="input"/>
      </label>
      <label v-if="type.endsWith('Img')">
        <div>输入在线图片地址：</div>
        <input type="text" v-model="msg" @keyup.enter="setHeadImg" class="input"/>
        <span class="tip" @click="search">
          懒得找？来这看看
        </span>
      </label>
      <label v-if="type === 'binaryImgSend'">
        <div>选择发送图片：{{ fileName }}</div>
        <div class="drag-area fx-pac" @dragover="fileDragover" @drop="fileDrop">
          <img :src="previewUrl" alt="预览" width="100%" v-if="fileName">
          <div v-else class="uploader-tips">
            <span>将文件拖拽至此，或</span>
            <label for="fileInput" style="color: #11A8FF; cursor: pointer">点此上传</label>
          </div>
        </div>
      </label>
      <label v-if="type === 'binaryFile'">
        <div>选择发送文件：</div>
        <div class="drag-area fx-pac" @dragover="fileDragover" @drop="fileDrop">
          <div v-if="fileName" class="file-name">{{ fileName }}</div>
          <div v-else class="uploader-tips">
            <span>将文件拖拽至此，或</span>
            <label for="fileInput" style="color: #11A8FF; cursor: pointer">点此上传</label>
          </div>
        </div>
      </label>
      <input id="fileInput" type="file" accept="image/*" style="display: none" @change="getFile">
      <span slot="footer" class="dialog-footer">
        <el-button @click="showBox = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import client from '../../utils/ConnectWebSocket'
import Message from './Message'
import HeadImg from "./HeadImg";
import pageManager from "@/utils/PageManager";

export default {
  name: "ChatRoom",
  components: {Message, HeadImg},
  computed: {
    tf() {
      return "matrix(" + this.transform.toString() + ")";
    }
  },
  data() {
    return {
      min: true,
      transform: [1, 0, 0, 1, 0, 0],
      messageList: client.getMessageList(),
      self: client.getSelf(),
      memberList: client.getMemberList(),
      nickNameMap: client.getNickNameMap(),
      headImgMap: client.getHeadImgMap(),
      privateMsgMap: client.getPrivateMsgMap(),
      unreadMsgCountMap: client.getUnreadMsgCountMap(),
      ChatRoom: pageManager.getAppInfo('ChatRoom'),
      text: "",
      talkBox: null,
      showBox: false,
      msg: '',
      type: '',
      groupImg: null,
      previewUrl: '',
      fileName: '',
      batchFile: null,
      emojiList: ['😀'],
      mouseDown: false,
      positionOnclick: [],
    }
  },
  created() {
    global.view = this;
    client.setChatRoom(this);
    this.emojiList = require('emojis-list');
  },
  mounted() {
    this.talkBox = document.getElementById('talkBox');
    let box = document.getElementById('header');
    box.addEventListener('mouseup', this.mouseup);
    box.addEventListener('mousedown', this.mousedown);
  },
  methods: {
    minimize() {
      pageManager.minimize("ChatRoom");
    },
    exit() {
      if (this.self.status === 'offline') {
        this.$message.error("请先连接！");
        return;
      }
      client.close();
    },
    connect() {
      if (this.self.status === 'onLine') {
        this.$message.error("已登录！");
        return;
      }
      this.messageList.length = 0;
      client.getConnection("chatroom");
      client.onmessage();
    },
    sendMsg() {
      if (client.client === null) {
        this.$message.error("请先连接！");
        return;
      }
      let Msg = {
        msg: this.text,
        sendTime: Date.now()
      }
      if (this.self.curTalk !== 'all') {
        Msg.type = 'private';
        Msg.destination = this.self.curTalk;
        Msg.self = true;
        Msg.clientName = this.self.clientName;
        this.messageList.push(Msg);
      }
      this.text = '';
      client.checkUnread(this, this.self.curTalk);
      if (this.self.clientName === Msg.destination) return;
      client.writeData(JSON.stringify(Msg));
    },
    privateChat(name) {
      this.self.curTalk = name;
      let msgList = this.privateMsgMap[name];
      if (!msgList) {
        this.$set(this.privateMsgMap, name, []);
        this.$set(this.unreadMsgCountMap, name, 0);
      }
      this.unreadMsgCountMap[name] = 0;
      this.messageList = this.privateMsgMap[name];
      client.checkUnread(this, name);
    },
    groupChat() {
      this.self.curTalk = 'all';
      this.unreadMsgCountMap['all'] = 0;
      this.messageList = client.getMessageList();
      client.checkUnread(this, name);
    },
    setNickname() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: 'nickname'
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.showBox = false;
    },
    openDialog(type) {
      if (this.self.status === 'offline') {
        this.$message.error("请先连接！");
        return;
      }
      this.type = type;
      this.showBox = true;
    },
    setHeadImg() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: this.type
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.showBox = false;
    },
    sendImg() {
      let file = this.batchFile
      let Msg = {
        clientName: this.self.clientName,
        sendTime: Date.now(),
        type: 'binaryImgSend',
      }
      Msg.destination = this.self.curTalk;
      if (this.self.clientName !== Msg.destination)
        client.writeData(JSON.stringify(Msg), file);
      Msg.self = true;
      Msg.img = URL.createObjectURL(file);
      this.messageList.push(Msg);
      this.showBox = false;
    },
    sendFile() {
      let file = this.batchFile
      let Msg = {
        msg: this.fileName,
        clientName: this.self.clientName,
        sendTime: Date.now(),
        type: 'binaryFile',
      }
      Msg.destination = this.self.curTalk;
      if (this.self.clientName !== Msg.destination)
        client.writeData(JSON.stringify(Msg), file);
      Msg.self = true;
      Msg.file = true;
      this.messageList.push(Msg);
      this.showBox = false;
    },
    submit() {
      switch (this.type) {
        case "nickname":
          this.setNickname();
          break;
        case "headImg":
        case "groupImg":
          this.setHeadImg();
          break;
        case "binaryImgSend":
          this.sendImg();
          break;
        case "binaryFile":
          this.sendFile();
          break;
      }
    },
    preview(file) {
      let suffix = file.name.split('.').pop();
      if (!['jpg', 'png', 'jpeg', 'gif'].includes(suffix)) {
        this.$message.error("文件类型有误！");
        this.fileName = null;
        return;
      }
      if (file) this.previewUrl = URL.createObjectURL(file);
    },
    fileDragover(e) {
      e.preventDefault()
    },
    getFile() {
      let file = document.getElementById('fileInput').files[0];
      if (!file) return;
      this.batchFile = file;
      this.fileName = file.name;
      if (this.type === 'binaryImgSend') this.preview();
    },
    fileDrop(e) {
      e.preventDefault()
      const file = e.dataTransfer.files[0] // 获取到第一个上传的文件对象
      if (!file) return
      if (file.size > 10 * 1024 * 1024) {
        return alert('文件大小不能超过10M')
      }
      this.batchFile = file;
      this.fileName = file.name;
      if (this.type === 'binaryImgSend') this.preview(file);
    },
    mousedown(ev) {
      pageManager.setFocusApp(this);
      this.positionOnclick[0] = this.transform[4];
      this.positionOnclick[1] = this.transform[5];
      this.positionOnclick[2] = ev.clientX;
      this.positionOnclick[3] = ev.clientY;
      this.mouseDown = true;
    },
    mouseup() {
      this.mouseDown = false;
    },
    closeApp() {
      pageManager.closeApp("ChatRoom")
    },
    search() {
      this.$emit('search');
    }
  }
}
</script>

<style scoped>
@import "../../assets/chatroom.css";
</style>