<template>
  <div class="desktop fx-pac">
    <!--    <component :is="item" v-for="(item,key) in runningApp" :key="key"-->
    <!--               :class="focusPage===item?'focus':'all'" @click.native="getFocus(item)"></component>-->
    <ChatRoom :class="focusPage==='ChatRoom'?'focus':'all'" @search="search" @click.native="getFocus('ChatRoom')"
              v-if="appMap['ChatRoom'].isLoad"></ChatRoom>
    <Brower ref="brower" :class="focusPage==='Brower'?'focus':'all'" @click.native="getFocus('Brower')"
            v-if="appMap['Brower'].isLoad"></Brower>

    <div class="shortcutBox fx-ver">
      <div class="fx-ver shortcut" @click="startApp('ChatRoom')">
        <span class="fa fa-wechat fz36"></span>
        <span>针针</span>
      </div>
      <div class="fx-ver fx-ac shortcut" @click="startApp('Brower')">
        <span class=" fa fa fa-edge fz36"></span>
        <span>浏览器</span>
      </div>
    </div>
    <div class="footer" style="z-index: 999;">
      <div class="start" v-if="openStart">
        <div class="start-select" @click="about">
          <span class="fa fa-smile-o fz22"></span><span>关于本站</span>
        </div>
      </div>
      <span class="system fa fa-windows fz22" title="开始" @click="openStart=!openStart"></span>
      <span v-for="(item,idx) in runningApp" :key="idx" :class="`shortcutMin fa ${appMap[item].shortcut} fz22`"
            @click="restore(item)"></span>
    </div>
  </div>
</template>

<script>
import ChatRoom from "@/components/ChatRoom/ChatRoom";
import Brower from "@/components/Brower/Brower";
import pageManager from "@/utils/PageManager";

export default {
  name: "Desktop",
  components: {ChatRoom, Brower},
  computed: {
    runningApp() {
      let tmp = [];
      for (let item in this.appMap) {
        if (this.appMap[item].isLoad)
          tmp.push(item);
      }
      return tmp;
    }
  },
  mounted() {
    document.body.addEventListener('mousemove', ev => {
      pageManager.mousemove(ev);
    });
  },
  data() {
    return {
      componentNames: ['ChatRoom'],
      appMap: pageManager.getAppMap(),
      minAppMap: pageManager.getMinAppMap(),
      focusPage: '',
      openStart:false
    }
  },
  methods: {
    restore(name) {
      this.focusPage = name;
      pageManager.restore(this.appMap[name]);
    },
    startApp(name) {
      this.appMap[name].isLoad = true;
      this.restore(name);
    },
    getFocus(name) {
      this.focusPage = name;
    },
    search() {
      this.startApp('Brower');
      setTimeout(() => {
        this.$refs['brower'].search1('https://image.baidu.com/search/index?ct=201326592&z=0&s=0&tn=baiduimage&ipn=r&word=%E5%A4%B4%E5%83%8F%E4%BA%8C%E6%AC%A1%E5%85%83&pn=&spn=&istype=2&ie=utf-8&oe=utf-8&cl=2&lm=-1&st=-1&fr=&fmq=1461834053046_R&ic=0&se=&sme=&width=&height=&face=0&cs=&os=&objurl=&di=&cg=head&gsm=5a');
        this.focusPage = "Brower"
      }, 300);
    },
    about(){
      this.$message.info("暂时没写！🌝")
    }
  }
}
</script>

<style scoped>
@import "../assets/app.css";

</style>