import client from "@/utils/ConnectWebSocket";

class BinaryHandler {
  binaryImgSend(that, data, msg) {
    msg.img = URL.createObjectURL(data);
    if (that.privateMsgMap[msg.clientName]) {
      that.privateMsgMap[msg.clientName].push(msg);
      client.checkUnread(that, msg.clientName);
    } else {
      that.privateMsgMap[msg.clientName] = [msg];
    }
  }
  
  binaryFile(that, data, msg) {
    msg.file = URL.createObjectURL(data);
    if (that.privateMsgMap[msg.clientName]) {
      that.privateMsgMap[msg.clientName].push(msg);
      client.checkUnread(that, msg.clientName);
    } else {
      that.privateMsgMap[msg.clientName] = [msg];
    }
  }
}

const binaryHandler = new BinaryHandler();
export default function (that, data) {
  data.slice(0, 4).arrayBuffer().then((buffer) => {
    let int32 = new DataView(buffer).getInt32(0);
    data.slice(4, 4 + int32).text().then((msgJson) => {
      console.log(msgJson);
      let msg = JSON.parse(msgJson);
      binaryHandler[msg.type](that, data.slice(4 + int32), msg);
    })
  });
}