<template>
  <div class="fx-ver mainBox1" :style="{'transform':tf }" v-show="Brower.min">
    <div id="header1" class="header1 fx-ac fx-pj">
      <div>
        <span class="fa fa-edge fz22"></span>
      </div>
      <div>
        <span class="browerName">浏览器丐中丐版</span>
      </div>
      <div class="fx-ac">
        <span class="fa fa-minus-square fz18 middleButton" @click="minimize"></span>
        <span class="fa fa-square fz18 middleButton"></span>
        <span class="fa fa-times fz18 middleButton" @click="closeApp"></span>
      </div>
    </div>
    <div class="fx-ac urlBox">
      <input class="urlInput" type="text" v-model="input" @keyup.enter="search">
    </div>
    <div style="height: 100%;background-color: #f8f9fa">
      <iframe id="iframe" :src="src" width="100%" height="100%"></iframe>
    </div>
  </div>
</template>

<script>
import pageManager from "@/utils/PageManager";

export default {
  name: "Brower",
  created() {
    window.myBrower = this;
  },
  mounted() {
    this.iframe = document.getElementById('iframe');
    let box = document.getElementById('header1');
    box.addEventListener('mouseup', this.mouseup);
    box.addEventListener('mousedown', this.mousedown);
    this.search();
  },
  computed: {
    tf() {
      return "matrix(" + this.transform.toString() + ")";
    }
  },
  data() {
    return {
      src: 'about:blank',
      input: 'www.bing.com',
      iframe: null,
      mouseDown: false,
      positionOnclick: [],
      Brower: pageManager.getAppInfo('Brower'),
      transform: [1, 0, 0, 1, 0, 0],
    }
  },
  methods: {
    search() {
      if (this.input.startsWith('http')) {
        this.src = this.input;
      } else {
        this.src = 'https://' + this.input;
      }
    },
    search1(url) {
      this.input = url;
      if (url.startsWith('http')) {
        this.src = url;
      } else {
        this.src = 'https://' + url;
      }
    },
    minimize() {
      pageManager.minimize("Brower");
    },
    closeApp() {
      pageManager.closeApp("Brower")
    },
    mousedown(ev) {
      pageManager.setFocusApp(this);
      this.positionOnclick[0] = this.transform[4];
      this.positionOnclick[1] = this.transform[5];
      this.positionOnclick[2] = ev.clientX;
      this.positionOnclick[3] = ev.clientY;
      this.mouseDown = true;
    },
    mouseup() {
      this.mouseDown = false;
    },
  }
}
</script>

<style scoped>
.mainBox1 {
  height: 80%;
  width: 90%;
}

.mainBox1:focus {
  z-index: 2;
}

.header1 {
  height: 40px;
  width: 100%;
  background-color: #48cbd4;
}

.urlInput {
  width: 80%;
}

.browerName {

}

.urlBox {
  background-color: #f8f9fa;
}
</style>