import client from "./ConnectWebSocket";

class TextHandler {
  talk(that, Msg) {
    Msg.self = Msg.clientName === that.self.clientName;
    client.messageList.push(Msg);
    client.checkUnread(that, 'all');
  }
  
  auth(that, Msg) {
    client.setSelf(Msg.clientName);
  }
  
  upLine(that, Msg) {
    that.memberList.list.push(Msg.clientName);
    that.memberList.list = [...that.memberList.list];
  }
  
  offLine(that, Msg) {
    that.memberList.list = that.memberList.list.filter(value => value !== Msg.clientName);
    delete that.privateMsgMap[Msg.clientName];
    delete that.unreadMsgCountMap[Msg.clientName];
  }
  
  nickname(that, Msg) {
    that.$set(that.nickNameMap, Msg.clientName, Msg.msg);
  }
  
  headImg(that, Msg) {
    that.$set(that.headImgMap, Msg.clientName, Msg.msg);
  }
  
  groupImg(that, Msg) {
    that.groupImg = Msg.msg;
  }
  
  private(that, Msg) {
    if (that.privateMsgMap[Msg.clientName]) {
      that.privateMsgMap[Msg.clientName].push(Msg);
      client.checkUnread(that, Msg.clientName);
    } else {
      that.$set(that.privateMsgMap, Msg.clientName, [Msg]);
      if (that.self.curTalk !== Msg.clientName)
        that.$set(that.unreadMsgCountMap, Msg.clientName, 1)
    }
  }
}

const textHandler = new TextHandler();
export default function (that, data) {
  let Msg = JSON.parse(data);
  textHandler[Msg.type](that, Msg)
}